#AdminPanelTitle{
    font-size: 22px;
    background-color: var(--mainColor);
    color: white;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
#AdminPanelLogOut{
    display: flex;
    align-items: flex-end;
    justify-content: center;
    color: var(--secondaryColor);
    font-size: 18px;
    cursor: pointer;

}
#AdminPanelLogOut:hover{
    color: white;
}